import {
  IServerResponse,
  IGetBackupJobStatsResponse,
  IGetRepositoryStatsResponse,
  IGetOverviewStatsResponse
} from "@/shared";
import { transformResponse } from ".";
import { axiosInstance } from "./axios";

export const getJobStats = async (
  userId: number,
  jobId: number
): Promise<IServerResponse<IGetBackupJobStatsResponse>> => {
  const response = await axiosInstance.get<
    IServerResponse<IGetBackupJobStatsResponse>
  >(`/user/${userId}/backupJob/${jobId}/stats`);
  return transformResponse(response);
};

